body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-top: 70px;
  padding-bottom: 60px;
}

p {
  margin: 0;
}

.btn {
  margin-right: 10px;
}

.container {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.main {
  margin-bottom: 50px;
}


.fa-sm {
  font-size: 1em;
}

.fa-sm > i {
  margin-right: 5px;
}
footer {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  height: 50px;
}

.flex-centered {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.backdrop {
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  left: 0;
  padding: 50px;
  position: fixed;
  right: 0;
  top: 0;
}

.modal-header {
  vertical-align: middle;
}

.modal-header h2 {
  display: inline-block;
  margin: 0;
}

.modal-container {
  background-color: #ffffff;
  border-radius: 5px;
  margin: 0 auto;
  padding: 3rem;
  position: relative;
  -webkit-transform: none;
          transform: none;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}

.modal-button {
  background-color: #007bff;
  border: 1px solid #007bff;
  border-radius: .3rem;
  color: #ffffff;
  cursor: pointer;
  width: 80%;
  padding: 2rem;
  font-size: 2rem;
  line-height: 2px;
  -webkit-transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.modal-body.iframe {
  height: 720px;
}

@media (min-width: 576px) {
  .modal-container {
    max-width: 900px;
    margin: 1.75rem auto;
  }
}


.fa {
  color: #007bff;
}

.fa.inline {
  display: inline-block;
}

.file-name {
  font-size: 26px;
  margin-right: 40px;
}
